import React, { Component } from 'react';
import './App.css';
import TaskFormLopHoc from './components/TaskFormLopHoc'
import TaskListLopHoc from './components/TaskListLopHoc'

class LopHoc extends Component {
    constructor(props) {
        super(props);
        this.state = {
            tasks: [],
            isDisplayForm: false,
        }
    }


    onToggleForm = () => {

        this.setState({
            isDisplayForm: !this.state.isDisplayForm
        })

    }

    onCloseForm = () => {
        this.setState({
            isDisplayForm: !this.state.isDisplayForm
        })
    }

    onClose = () => {
        this.setState({
            isDisplayForm: false
        })
    }

    /* LocalStorage  */
    componentDidMount() {
        if (localStorage && localStorage.getItem('tasks')) {
            var tasks = JSON.parse(localStorage.getItem('tasks'));
            this.setState({
                tasks: tasks
            });
        }
    }

    onReset = () => {
        var tasks = []
        this.setState({
            tasks: tasks
        })
        localStorage.setItem('tasks', JSON.stringify(tasks));
    }


    s4() {
        return Math.floor(1 + Math.random() * 0x10000).toString(16).substring(1);
    }

    generateID() {
        return this.s4() + this.s4() + '-' + this.s4() + '-' + this.s4() + '-' + this.s4() + '-'
            + this.s4() + this.s4() + this.s4();
    }


    onSubmit = (data) => {
        console.log(data);
        var { tasks } = this.state;
        data.id = this.generateID();
        if (data.tenkhoi !== "" && data.tenlop !== "" && data.giaovien !== "" && data.chuyenmon !== "") {
            tasks.push(data);
        }
        this.setState({
            tasks: tasks
        })
        localStorage.setItem('tasks', JSON.stringify(tasks));
        this.onCloseForm();
    }

    onShowForm = () => {
        this.setState({
            isDisplayForm: true
        })
    }

    onChange = (event) => {
        var target = event.target;
        var name = target.name;
        var value = target.value;
        this.setState({
            [name]: value
        })
    }

    findIndex = (id) => {
        var { tasks } = this.state;
        var result = -1;
        tasks.forEach((task, index) => {
            if (task.id === id) {

                result = index;
            }
        });
        return result;
    }

    onDeleteLopHoc = (id) => {
        var { tasks } = this.state;
        var index = this.findIndex(id);
        if (index !== -1) {
            tasks.splice(index, 1);
            this.setState({
                tasks: tasks
            });
            localStorage.setItem('tasks', JSON.stringify(tasks));
        }
        this.onClose();
    }

    onUpdateLopHoc = (id) => {
        var { tasks } = this.state;
        var index = this.findIndex(id);
        var taskEditing = tasks[index];
        this.setState({
            taskEditing: taskEditing
        })
        this.onShowForm();
    }
    render() {

        var { isDisplayForm, tasks, taskEditing } = this.state;

        var elmTaskFromLopHoc = isDisplayForm
            ? <TaskFormLopHoc
                onCloseForm={this.onCloseForm}
                onSubmit={this.onSubmit}
                tasks={taskEditing}
            />
            : ""


        return (
            //   <div>

            // menu


            <div className='Form-app'>
                <div className="abc">
                    <div className="row">
                        <h1>Danh sách các lớp học</h1>
                        <div className={isDisplayForm ? "col-xs-3 col-sm-3 col-md-3 col-lg-3 mt-50" : ""}>
                            { /* Form Add LopHoc */}
                            {elmTaskFromLopHoc}

                        </div>

                        <div className={isDisplayForm
                            ? "col-xs-9 col-sm-9 col-md-9 col-lg-9 mt-50"
                            : "col-xs-12 col-sm-12 col-md-12 col-lg-12 mt-50"}
                        //  className = {isDisplaySideBar === false 
                        //         ? "col-xs-8 col-sm-8 col-md-8 col-lg-8 abcd mt-50" 
                        //         : "col-xs-10 col-sm-10 col-md-10 col-lg-10 abcd mt-50"}
                        >




                            <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12" style={{ paddingLeft: 0 }}>
                                <button type="button"

                                    className="btn btn-primary mb-5 "
                                    onClick={this.onToggleForm}
                                >
                                    <i className="fas fa-plus mr-5"></i>
                                                        Thêm lớp học
                                                    </button>&nbsp;
                                                   </div>


                            {/* <button type="button" 
                                                            className="btn btn-danger mb-5 "
                                                            onClick = {this.onReset}>
                                                        
                                                        Reset All
                                                    </button> */}

                            {/* Task List*/}
                            <TaskListLopHoc tasks={tasks}
                                onDeleteLopHoc={this.onDeleteLopHoc}
                                onUpdateLopHoc={this.onUpdateLopHoc} />

                        </div>
                    </div>

                </div>
            </div>


            //    </div>



            //   </div>       


        );
    }
}



export default LopHoc;